import "./styles.css";
const colors = ["#1abc9c", "#3498db", "#9b59b6", "#f39c12", "#e74c3c"];
// <‚ö†Ô∏è /DONT DELETE THIS ‚ö†Ô∏è>

/*
‚úÖ The text of the title should change when the mouse is on top of it.
‚úÖ The text of the title should change when the mouse is leaves it.
‚úÖ When the window is resized the title should change.
‚úÖ On right click the title should also change.
‚úÖ The colors of the title should come from a color from the colors array.
‚úÖ DO NOT CHANGE .css, or .html files.
‚úÖ ALL function handlers should be INSIDE of "superEventHandler"
*/

const title = document.querySelector("h2");

const superEventHandler = {
  handleMouseEnter: function () {
    title.style.color = colors[0];
    title.innerText = "Mouse Enter";
  },
  handleMouseLeave: function () {
    title.style.color = colors[1];
    title.innerText = "Mouse Leave";
  },
  handleOnResize: function () {
    title.style.color = colors[2];
    title.innerText = "On Resize";
  },
  handleContextMenu: function () {
    title.style.color = colors[3];
    title.innerText = "Context Menu";
  },
};

title.addEventListener("mouseenter", superEventHandler.handleMouseEnter);
title.addEventListener("mouseleave", superEventHandler.handleMouseLeave);
window.addEventListener("resize", superEventHandler.handleOnResize);
window.addEventListener("contextmenu", superEventHandler.handleContextMenu);

